.avatar-container {
  transition: visibility 1s, transform 1s, opacity 1s, position 1s;
}

.avatar-container.hidden {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  transform: translateY(25%);
}

.intro-parent {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.25fr 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  height: 90vh;

}
.intro-div1 {
  grid-area: 1 / 1 / 2 / 2;
}
.intro-div2 {
  grid-area: 2 / 1 / 3 / 2;
}

@font-face {
  font-family: 'KaushanScript-Regular';
  src: local('KaushanScript-Regular'),
       url('./fonts/KaushanScript-Regular.woff2') format('woff2');
}

.projects-global {
  justify-content: space-evenly; /* Center horizontally */
  text-align: center;
  align-items: center; /* Center vertically */
  margin-bottom: 7vh;
}

.project-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1280px; /* Instead of a fixed width, use max-width to make it responsive */
  margin: 0 auto; /* Center the container horizontally */
  gap: 3em;
  justify-content: center;
}

.article-global {
  justify-content: space-evenly; /* Center horizontally */
  text-align: center;
  align-items: center; /* Center vertically */
  margin-bottom: 7vh;
}

.article-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1280px; /* Instead of a fixed width, use max-width to make it responsive */
  margin: 0 auto; /* Center the container horizontally */
  gap: 3em;
  justify-content: center;
  margin-top: 3em;
}

.code-snippets-code-block {
  background-color: rgb(32, 32, 32);
  /* color: white; */
  font-family: 'Courier New', monospace;
  padding: 10px;
  border-radius: 5px;
}

.code-snippets-code-block p {
  margin: 0;
}

.code-snippets-comment {
  color: #00dcff;
}

.code-snippets-string {
  color: #CE9178;
}

.code-snippets-email {
  color: #D19A66;
}